.hero {
  background-color: $white;
  margin-bottom: 20px;
  &__inner {
    position: relative;
    padding-top: 150px;
    padding-bottom: 191px;
    @include md {
      padding-top: 100px;
      padding-bottom: 150px; }
    @include sm {
      text-align: center;
      padding-bottom: 0;
      padding-top: 30px; }
    .hero_small & {
      padding-top: 96px;
      padding-bottom: 96px; } }
  &__bg {
    @include coverdiv;
    z-index: 1;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: right top;
    @include sm {
      position: static;
      width: 100%;
      min-height: 300px;
      background-position: center; } }

  &__content {
    position: relative;
    display: inline-block;
    padding-left: 160px;
    text-align: center;
    z-index: 2;
    @include md {
      padding-left: 80px; }
    @include sm {
      padding-left: 0; } }
  &__title {
    margin-bottom: 21px;
    @include md {
      margin-bottom: 14px; }
    @include sm {
      margin-bottom: 7px; } }
  &__text {
    color: $text;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.4; } }
