.btn-link {
  display: inline-block;
  color: $blue;
  background: none;
  border: none;
  font-family: $montserrat;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
  transition: color $tr;
  @include hover {
    color: lighten($blue, 10%); } }

.btn {
  display: inline-block;
  padding: 13px 10px;
  max-width: 207px;
  width: 100%;
  text-align: center;
  color: $white;
  font-family: $montserrat;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
  border-radius: $radius;
  transition: background-color $tr, box-shadow $tr, transform $tr;
  @include md {
    max-width: 180px; }
  &_extraorange {
    background-color: $extraorange;
    @include hover {
      background-color: darken($extraorange, 5%);
      &:active {
        box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
        transform: translateY(-1px); } } }
  &_blue {
    background-color: $blue;
    @include hover {
      background-color: darken($blue, 5%);
      &:active {
        box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
        transform: translateY(-1px); } } }
  &_orange {
    background-color: $orange;
    @include hover {
      background-color: darken($orange, 5%);
      &:active {
        box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
        transform: translateY(-1px); } } } }

.like {
  @include circle(30);
  background-color: $purple;
  position: relative;
  display: inline-block;
  transition: background-color $tr;
  .icon {
    @include vcenter;
    font-size: 8px;
    color: $white; } }

.up-btn {
  display: inline-block;
  @include vertical;
  @include circle(24);
  font-size: 0;
  background-color: $blue;
  @include sm {
    @include circle(18); }
  .icon {
    @include icenter;
    transform: rotate(90deg);
    transform-origin: center;
    font-size: 7px;
    color: $white; } }

.delete-btn {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
  background-color: transparent;
  .icon {
    font-size: 6px;
    color: $text; } }
