%font {
  @extend %baseFont;
  color: rgba($white, 0.5);
  font-weight: 500; }

.footer {
  background-color: $blackBG;
  padding: 80px 0;
  position: relative;
  @include sm {
    padding: 80px 0 40px; }
  &__wrapper {
    font-size: 0;
    display: flex;
    flex-wrap: wrap;
    @include xs {
      flex-direction: column; } }
  &__title {
    color: $white;
    margin-bottom: 32px;
    @include sm {
      margin-bottom: 5px; } }
  &__col {
    // display: inline-block
    // width: 25%
    @include flex-size(25%);
    vertical-align: top;
    @include sm {
      @include flex-size(50%);
      margin-bottom: 20px; }
    @include xs {
      @include flex-size(100%);
      text-align: center; }
    &_logo {
      text-align: right;
      @include sm {
        position: absolute;
        top: 40px;
        left: 15px;
        transform: translateY(-50%);
        text-align: left; } }
    p {
      @extend %font;
      margin-bottom: 20px; } }
  &__form {
    max-width: 180px;
    display: inline-block; } }

.footer-list {
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
      @include sm {
        margin-bottom: 5px; } } }
  &__link {
    @extend %font; }
  @include sm {
    br {
      display: none; } } }
