.shop-page {
  // margin-top: $headerHeight
  &__inner {
 } }    // background-color: $orange

.product {
  margin-bottom: 12px;
  display: flex;
  @include sm {
    flex-direction: column;
    align-items: center; }
  &__slider {
    @include flex-size(320px);
    @include sm {
      margin-bottom: 24px;
      @include flex-size(290px); } }
  &__card {
    flex-grow: 1;
    padding-left: 10px;
    @include sm {
      padding-left: 0; }
    .product-card {
      height: 100%; } } }

.product-card {
  padding: 40px 72px 45px 27px;
  background-color: $white;
  border-radius: $radius;
  @include md {
    padding: 30px 20px; }
  &__title {
    margin-bottom: 8px; }
  &__prices {
    @extend .title-lg;
    margin-bottom: 18px; }
  &__current-price {
    display: inline-block;
    margin-right: 10px; }
  &__old-price {
    display: inline-block;
    color: #e1dddd;
    text-decoration: line-through; }
  &__like {
    margin-bottom: 24px;
    @include md {
      margin-bottom: 8px; } }
  &__info {
    margin-bottom: 30px;
    .product-code,
    .product-category {
      @include icenter; }
    .product-code {
      margin-right: 30px; } }
  &__descr {
    margin-bottom: 30px; }
  &__actions {
    font-size: 0;
    .choose {
      margin-right: 20px; } } }

.product-category {
  @extend .title;
  &__category {
    text-transform: uppercase; } }

.product-descr-card {
  padding: 30px 40px 30px 15px;
  margin-bottom: 33px;
  background-color: $white;
  border-radius: $radius;
  @include sm {
    padding: 30px 20px; }
  p {
    &:not(:last-child) {
      margin-bottom: 22px; } } }
