.availability {
  text-align: center;
  margin-bottom: 20px;
  &__wrapper {
    padding: 91px 20px 40px;
    border-radius: $radius;
    background-color: $cardBG;
    box-shadow: $lightShadow;
    @include md {
      padding-top: 70px; } }
  &__icon-wrapper {
    margin-bottom: -80px;
    @include md {
      margin-bottom: -40px; } }
  &__icon {
    display: inline-block;
    @include circle(100);
    @include vertical;
    @include md {
      @include circle(60); }
    .icon {
      @include icenter;
      font-size: 42px;
      color: $white;
      @include md {
        font-size: 32px; } } }
  &__text {
    display: inline-block;
    max-width: 480px;
    margin-bottom: 20px; } }
