.card {
  width: 200px;
  text-align: center;
  @include md {
    width: 150px; }
  &__image-wrapper {
    display: block;
    position: relative;
    margin-bottom: 20px;
    border-radius: $radius;
    box-shadow: $darkShadow;
    overflow: hidden;
    @include md {
      margin-bottom: 10px; } }
  input[type='checkbox'] {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    height: 0;
    &:checked {
      ~ .card__like {
        .like {
          background-color: $purple; } } } }
  &__image {
    padding-bottom: 100%;
    background-color: $cardBG;
    @include cover; }
  &__sale-mark {
    display: inline-block;
    padding: 4px 7px;
    background-color: $purple;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 600;
    color: $white;
    line-height: 1.3;
    position: absolute;
    top: 0;
    left: calc(100% - 11px);
    transform: rotate(90deg);
    transform-origin: 0 0; }
  &__title {
    margin-bottom: 8px;
    @include md {
      margin-bottom: 0; } }
  &__price {
    margin-bottom: 10px;
    color: $text;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.15;
    @include md {
      margin-bottom: 0; } }
  &__like {
    position: absolute;
    bottom: 10px;
    right: 12px;
    font-size: 0;
    .like {
      background-color: #e1dddd; } } }
